import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState } from '../index'
import { roladPage } from './actions'

export function useDrawBoxState(): AppState['DrawBox'] {
  return useSelector<AppState, AppState['DrawBox']>((state) => state.DrawBox)
}

export function useDrawBoxHandlers(): {
  onIsRefresh:(isRefresh:boolean)=>void
} {
  const dispatch = useDispatch<AppDispatch>()
  const onIsRefresh = useCallback((isRefresh:boolean) => {
    dispatch(roladPage({isRefresh}))
  }, [dispatch])

  return { onIsRefresh }
}
